<template>
    <section class="pc-container pb-4 px-4">
        <div class="pcoded-content">
            <div class="card shadow">
                <div class="card-header py-3">
                    <div class="d-flex align-items-center">
                        <h5>Lista de Materias</h5>
                        <button class="btn btn-success btn-sm px-4 ml-auto rounded-1"  @click="mostrarModal">
                            Nuevo
                        </button>
                    </div>
                </div>
                <div class="card-body">
                    <div class="table-responsive">
                        <table class="table" id="table">
                            <thead>
                                <tr>
                                    <th class="text-center pl-4 col-lg-2 col-sm-1">#</th>
                                    <th>Nombre</th>
                                     <th class="text-center pl-4 col-lg-2 col-sm-1">#</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(materia, index) in filteredMaterias" :key="materia.mat_id">
                                    <td class="text-center py-2">
                                        <input class="form-check-input me-1" type="checkbox" value="" />
                                    </td>
                                    <td>{{ materia.mat_nombre }}</td>
                                    <td class="text-center">
                                        <a href="javascript:void(0)" @click="editar(materia.mat_id)"
                                            class="text-primary px-2 py-1 mx-2">
                                            <i data-feather="edit"></i>
                                        </a>
                                        <a href="javascript:void(0)" @click="borrar(materia.mat_id)"
                                            class="px-2 py-1 btn-lg text-danger">
                                            <span>
                                                <i data-feather="trash"></i>
                                            </span>
                                        </a>
                                    </td>
                                </tr>
                                <tr v-if="filteredMaterias.length === 0">
                                    <td colspan="3" class="text-center">No se encontraron resultados</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="showModal" class="modal" @click="cerrarModal">
            <div class="modal-content card border-0" @click.stop>
                <div class="card-header">
                    <h5 class="text-center">
                        {{ materia.mat_id ? 'Actualizar Materia' : 'Registro de nueva Materia' }}
                    </h5>
                </div>
                <div class="card-body mb-0 pb-0">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="form-group">
                                <label class="form-label">Nombre</label>
                                <input class="form-control" v-model="materia.mat_nombre" @input="validarNombre"
                                    :class="{ 'has-error': errores.mat_nombre }" @keyup.enter="guardar">
                                <span class="error-message" v-if="errores.mat_nombre">Ingrese un nombre de
                                    materia.</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="card-footer border-0 mt-0 pt-0">
                    <div class="d-flex justify-content-end">
                        <button class="btn btn-secondary btn-lg  px-4 mx-2 rounded" @click="cerrarModal">Cerrar</button>
                        <button class="btn btn-primary btn-lg  px-4 rounded" @click="guardar">
                            {{ materia.mat_id ? 'Actualizar' : 'Guardar' }}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { mapActions } from 'vuex';

export default {
    name: "materias",
    created() {
        this.leer();
    },
    data() {
        return {

            dataTable: null,
            showModal: false,
            searchQuery: '',
            materias: [
            ],
            materia: {
                mat_id: "",
                mat_nombre: ""
            },
            errores: {
                mat_nombre: false
            }

        };
    },
    computed: {
        filteredMaterias: function () {
            const query = this.searchQuery.toLowerCase().trim();
            if (!query) {
                return this.materias;
            } else {
                return this.materias.filter((materia) =>
                    materia.mat_nombre.toLowerCase().includes(query)
                );
            }
        },
    },
    updated() {
        feather.replace();
    },

    methods: {
        ...mapActions(["get", "post"]),

        // Validacion de campos
        validarCampo(campo, propiedadError) {
            if (this.materia[campo] === "") {
                this.errores[propiedadError] = true;
            } else {
                this.errores[propiedadError] = false;
            }
        },

        validarNombre() {
            this.materia.mat_nombre = this.materia.mat_nombre;
            this.validarCampo('mat_nombre', 'mat_nombre');
        },

        validarCampos() {
            this.validarNombre();

            return !Object.values(this.errores).some(error => error);
        },

        async leer() {
            try {
                const response = await this.get({
                    url: this.$store.getters.get__url + "/subject",
                    token: this.$store.getters.get__token,
                });

                console.log(response);
                this.materias = response.data;
                this.initDataTable();
            } catch (error) {
                console.error("Error al intentar obtener los datos", error);
            }
        },

        async crear() {
            if (!this.validarCampos()) {
                console.error('Error en la validación de los campos del formulario de registro.');
                return;
            }
            try {
                const response = await this.post({
                    url: `${this.$store.getters.get__url}/subject/store`,
                    token: this.$store.getters.get__token,
                    params: this.materia
                });
                if (response.state === 0) {
                    this.showModal = false;
                    await this.leer();
                    this.materia.mat_nombre = "";
                } else {
                    console.error('Error en la respuesta del servidor:', response.message);
                }
            } catch (error) {
                console.error('Error en la solicitud:', error);
            }
        },

        async actualizar(id) {
            if (!this.validarCampos()) {
                console.error('Error en la validación de los campos del formulario de registro.');
                return;
            }
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/subject/update",
                    token: this.$store.getters.get__token,
                    params: {
                        mat_id: id,
                        mat_nombre: this.materia.mat_nombre
                    }
                });

                if (response.state == 0) {
                    this.showModal = false;
                    await this.leer();
                } else {
                    console.log("error");
                }
            } catch (errors) {
                console.error(errors);
            }
        },

        async borrar(id) {

            try {
                alert('Seguro de eliminar.');
                const response = await this.post({
                    url: this.$store.getters.get__url + "/subject/destroy",
                    token: this.$store.getters.get__token,
                    params: {
                        mat_id: id
                    }
                });

                if (response.state == 0) {
                    await this.leer();
                } else {
                    console.log("error");
                }
            } catch (errors) {
                console.error(errors);
            }
        },

        editar(id) {
            try {
                const materiaEdit = this.materias.find(materia => materia.mat_id === id);
                if (materiaEdit) {
                    this.materia = { ...materiaEdit };
                    this.showModal = true;
                } else {
                    console.log("Materia no encontrada");
                }
            } catch (errors) {
                console.error(errors);
            }
        },

        guardar() {
            try {
                if (!this.materia.mat_id) {
                    this.crear();
                    console.log("crear");
                } else {
                    this.actualizar(this.materia.mat_id);
                }
            } catch (error) {
                console.error(error);
            }
        },

        initDataTable() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }

            this.$nextTick(() => {
                this.dataTable = $('#table').DataTable({
                    language: {
                        url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
                    },
                });
                feather.replace();
            });
        },

        mostrarModal() {
            this.showModal = true;
        },

        cerrarModal() {
            this.showModal = false;
            this.errores.mat_nombre = false;
            this.materia.mat_id = "";
            this.materia.mat_nombre = "";
        }

    },
};
</script>
  
<style scoped>
td {
    white-space: normal !important;
    word-wrap: break-word;
}

.tooltip-trigger {
    cursor: pointer;
    width: 2.25rem;
    height: 2.25rem;
    border: none;
    background-color: #7267EF;
    color: #fff;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 0.55rem;
}

.circle {
    width: 30px;
    height: 30px;
    background-color: #7267EF;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
}

/* Estilo para el selector de cantidad de datos */
.dataTables_length {
    margin-bottom: 20px !important;
    /* Ajusta el espacio entre el selector y la tabla */
}

/* Estilo para las etiquetas en el selector de cantidad de datos */
.dataTables_length label {
    font-weight: bold;
    margin-right: 10px;
    /* Ajusta el espacio entre las etiquetas y los botones */
}

/* Estilo para los botones en el selector de cantidad de datos */
.dataTables_length select {
    padding: 4px;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: #fff;
    width: 500px;
    height: auto !important;
    border-radius: 8px !important;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    position: relative !important;
}

/* 

@media (max-width: 768px) {
    .modal-content {
        width: 90%;
    }
}

.card-header {
    border-radius: 8px 8px 0px 0px !important;
} */

.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    padding-left: 15px;
    color: #EA4D4D;
    font-size: 12px;
}
</style>
  